import React from 'react';
import moment from "moment/min/moment-with-locales";
import {Link} from "react-router-dom";
import store from "../store";

const Post = ({ post }) => {
    return (
        <Link to={'blog/'+post.slug} className={'post'}>
            <span className="post__category">#{store.getCategoryName(post.categories[0])}</span>
            <h2 className="post__title">{post.title.rendered}</h2>
            <p className="post__description" dangerouslySetInnerHTML={{__html: post.excerpt.rendered}}></p>
            <div className="post__date">Посл. обновление: {moment(post.modified).locale('ru').format('LL')}</div>
        </Link>
    );
};

export default Post;