import React from 'react';
import store from "../store";
import {observer} from "mobx-react-lite";

const Loader = () => {
    const start = () => {
        store.setStarted(true)
        const audioEl = document.getElementsByClassName("audio-element")[0]
        audioEl.volume = 0.4;
        audioEl.play()
    }

    return (
        <div className={'loader ' + (store.started ? 'hide' : '')}>
            <div className="loader__img">
                <img src="http://wp-riva-site/wp-content/uploads/2023/07/pngitem_1377229.png" alt="dwayne"/>
            </div>
            <div className="loader__text">
                Catch him!
            </div>
            <button className="loader__btn" onClick={start} disabled={!store.posts.length}>{!store.posts.length ? 'Wait...' : 'Ok!'}</button>
        </div>
    );
};

export default observer(Loader);