import React, {useEffect} from 'react';
import Post from "./Post";
import store from "../store";
import {observer} from "mobx-react-lite";

const Posts = () => {
    const filteredPosts = store.posts.filter(({categories, title}) => {
        return (store.category ? categories.includes(store.category) : true)
            && title.rendered.search(store.query) !== -1
    })

    useEffect(() => {
        store.fetchPosts()
    }, [])

    return (
        <div className={'posts'}>
            {filteredPosts.map(post => <Post key={post.id} post={post} />)}
        </div>
    );
};

export default observer(Posts);