import React from 'react';
import {Container} from "react-bootstrap";
import {Link} from "react-router-dom";
import store from "../store";
import {observer} from "mobx-react-lite";

const Footer = () => {
    return (
        <footer className={'footer'}>
            <Container>
                <div className="footer__wrapper">
                    <div className="footer__left">
                        <Link to={'/'} className="logo">{store.title}</Link>
                        <div className="footer__text">
                            This blog is a simple pet-project to show my WordPress skills.
                            Some of the design was taken from <a href="https://demo-blog.caasy.io" target={'_blank'} rel="noopener noreferrer" className="link">Caasy Blog</a>.
                        </div>
                        <div className="footer__socials">
                            {store.socials.map((social) => <a href={social.link} key={social.slug} className="footer__social"><social.icon/></a>)}
                        </div>
                    </div>
                    <div className="footer__menu">
                        <Link to={'/about'} className="footer__link">About this site</Link>
                        <a href={'/wp-admin'} className="footer__link">Go to Admin</a>
                    </div>
                </div>
            </Container>
        </footer>
    );
};

export default observer(Footer);