import {makeAutoObservable} from "mobx";
import {API_URL} from "../config";
import {AiFillGithub, AiFillInstagram} from "react-icons/ai";
import {FaTelegram} from "react-icons/fa";

class Store {
    posts = []
    title = ''
    query = ''
    socials = []
    categories = []
    category = false
    visibleDwayne = true
    started = false

    constructor() {
        makeAutoObservable(this)

        this.fetchPosts()
        this.fetchSocials()
        this.fetchCategories()
        this.fetchTitle()
    }

    makeApiCall(endpoint, fields=[], add='') {
        return fetch(`${API_URL}${endpoint}${fields.length ? '?per_page=100&_fields='+fields.join(',') : ''}${add}`).then(rs => rs.json())
    }

    fetchPosts() {
        const fields = [
            'id',
            'excerpt',
            'title',
            'slug',
            'modified',
            'content',
            'categories',
        ]

        this.makeApiCall('posts', fields).then(posts => {
            this.posts = posts
        })
    }

    fetchPost(slug) {
        const fields = [
            'id',
            'excerpt',
            'title',
            'slug',
            'modified',
            'content',
            'categories',
        ]

        return this.makeApiCall('posts', fields, '&slug='+slug)
    }

    fetchCategories() {
        const fields = [
            'id',
            'name',
        ]

        this.makeApiCall('categories', fields).then(categories => {
            this.categories = categories.filter(({name}) => name !== 'Без рубрики')
        })
    }

    fetchSocials() {
        const fields = [
            'slug',
            'acf',
        ]

        const icons = {
            instagram: AiFillInstagram,
            telegram: FaTelegram,
            github: AiFillGithub,
        }

        this.makeApiCall('social', fields).then(socials => {
            for (let i=0;i<socials.length;i++) {
                socials[i]['icon'] = icons[socials[i].slug]
                socials[i]['link'] = socials[i].acf.link
            }

            this.socials = socials
        })
    }

    fetchTitle() {
        const fields = [
            'title',
        ]

        this.makeApiCall('settings', fields).then(({title}) => {
            this.title = title
        })
    }

    getPost(_slug) {
        return this.posts.find(({slug}) => slug === _slug)
    }

    setCategory(id) {
        this.category = id === this.category ? false : id
    }

    setQuery(e) {
        this.query = e.target.value
    }

    getCategoryName(_id) {
        return this.categories.find(({id}) => id === _id)?.name
    }

    setVisibleDwayne(bool) {
        this.visibleDwayne = bool
    }

    setStarted(bool) {
        this.started = bool
    }
}

export default new Store()